import React from "react"
import { useStaticQuery, graphql } from "gatsby";
import Img from "gatsby-image"

import Layout from "../components/layout"
import HomeProducts from "../components/HomeProducts/HomeProducts.component";
import HomeTestimonials from "../components/HomeTestimonials/HomeTestimonials.component";
// import Image from "../components/image"
import SEO from "../components/seo"

const HomePage = () => {

  const data = useStaticQuery(graphql`
      query {
        allMarkdownRemark(filter: {frontmatter: {title: {eq: "Home"}}}) {
          edges {
            node {
              frontmatter {
                description
                title
                headImage {
                  childImageSharp {
                    fluid (maxWidth: 1920) {
                      ...GatsbyImageSharpFluid_noBase64
                    }
                  }
                }
              }
              html
            }
          }
        }
      }
  `)
  

  return(
    <Layout>
    {
      data.allMarkdownRemark.edges.map(({node}) => (
        <div>
          <SEO title={ node.frontmatter.title } description={ node.frontmatter.description } />
          <Img fluid={ node.frontmatter.headImage.childImageSharp.fluid } />
          <HomeProducts homeContent={ node.html } />
          <HomeTestimonials />
        </div>
      ))
    }
    
  </Layout>
  )
}

export default HomePage;